.song-image{
    height: 6.9rem;
    width: 7rem;
    border-radius: 11px;
    margin: 1rem;
    border: 3px solid black;
}

.one-song{
    box-shadow: 1px 1px 3px 0.1px black;
    border-radius: 10px;
    margin-bottom: 8px;
    width: 50rem;
    margin-left: 4rem;
    transition: transform .2s;
}

.one-song:hover{
    box-shadow: none;
    border: 2px solid orangered;
    border-radius: 10px;
    transform: scale(1.04);
}

.nav-design{
    background-color: orangered;
}

.nav-button{
    margin-left: 2rem;
    font-size: 17px;
    color: white;
}

.nav-button:hover{
    color: black;
    background-color: white;
    padding: 1.5rem;
    border-radius: 0 0 0.5rem 0.5rem;
}

.nav-options{
    text-decoration: none;
}

.nav-look{
    padding: 0.8rem;
}
.search-input{
    width: 57rem;
}
.search-portion{
    margin-top: 4rem;
}

progress{
    width: 28rem;
    height: 2rem;
}

.progress-bar{
    margin: 13.8rem 6rem 6rem 2rem;
    position: fixed;
    box-shadow: 1px 1px 3px 0.1px black;
    border-radius: 1rem;
    padding: 2rem;
}
.main-home{
    display: flex;
}
.progress-word{
    text-align: left;
}
.top-music{
    padding-bottom: 1rem;
}

.music-player{
    margin: 6rem 0 0 1.5rem;
}

.music-play{
    margin: 1rem 0 0 34rem;
    box-shadow: 1px 1px 3px 0.1px black;
    border-radius: 1rem;
    width: 28rem;
    padding: 2rem;
}

.music-pic{
    height: 11rem;
    width: 11rem;
    border-radius: 11px;
    margin: 1rem 0 0 6.7rem;
    border: 3px solid black;
}

.track-name{
    margin-top: 1rem;
    text-align: center;
    font-size: 1.05rem;
}

.music-play-heading{
    margin-left: 6.2rem;
    font-size: 2rem;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

audio{
    margin-left: 2.4rem;
}

.brand-name{
    color: white;
}

.complete-section{
    margin-top: 4rem;
}

.pic{
    height: 22rem;
    width: 30rem;
}

.top-music-india{
    box-shadow: 1px 1px 3px 0.1px black;
    border-radius: 10px;
    margin: 5rem 0 5rem 12rem;
    padding: 1rem;
}

.top-music-list{
    text-align: center;
    padding: 1rem 0 2.5rem 0;
}

.music-data{
    font-size: 1.4rem;
}

.loader{
    margin: 0 0 1rem 20rem;
}

@media (min-width:0) and (max-width:1490px){
    .progress-bar{
        display: none;
    }

    .search-input{
        width: 33rem;
    }

    .one-song{
        width:33rem;
        margin: 0 1rem;
    }

    .loader{
        margin-left: 9rem;
    }

    .top-music-india{
        margin: 5rem 0 5rem 3rem;
        padding: 2rem;
    }
}